import s from './Post.module.css'

const Post = (props) => {
    return (
        <div className={s.posts}>
            <div className={s.item}>
                <img
                    src='data:image/jpeg;base64,'/>
                {props.message}
                <div>
                    <span className={s.like}>like {props.likesCount}</span>
                </div>
            </div>
        </div>
    )
};
export default Post
